@charset "UTF-8";
/*프로모션 색상*/
/*widget_grid 배경색상*/
/*가변 마진 공식 (적용한 마진값 /겉박스)*100 */
/*가변 vw 공식 ((원하는 값)*100/페이지 width크기)*/
.third-section {
  width: 100%;
  padding: 130px 0;
  background-color: #f6f4fc;
  font-size: 16px; }
  .third-section--content {
    width: 70%;
    margin: 0 auto;
    -webkit-transition: all 0.45s ease-out;
    -moz-transition: all 0.45s ease-out;
    -ms-transition: all 0.45s ease-out;
    -o-transition: all 0.45s ease-out;
    transition: all 0.45s ease-out; }
    .third-section--content--list {
      float: left;
      width: 30.80357142857143%;
      height: 593px;
      background-color: #fff;
      margin-right: 3.794642857142857%;
      -webkit-border-radius: 10px;
      -moz-border-radius: 10px;
      border-radius: 10px; }
      .third-section--content--list > div {
        padding: 0 62px;
        text-align: center; }
        .third-section--content--list > div img {
          width: 100%;
          max-width: 120px;
          min-width: 64px;
          max-height: 120px;
          min-height: 120px;
          height: auto;
          display: inline-block;
          margin-top: 110px; }
        .third-section--content--list > div h1 {
          margin-top: 3.65vw;
          font-size: 1.25vw;
          font-weight: bold; }
        .third-section--content--list > div p {
          margin: 30px auto 0;
          text-align: center;
          font-size: 0.75vw;
          line-height: 1.88;
          font-weight: 300;
          opacity: .7; }
    .third-section--content--list:nth-child(3) {
      margin: 0; }

/*레티나 해상도*/
@media only screen and (min-width: 2560px) {
  .third-section--content--list {
    height: 800px; } }

@media only screen and (max-width: 1440px) {
  .third-section--content {
    width: 80%; }
  .third-section--content--list > div {
    padding: 0 30px; } }

@media only screen and (max-width: 1024px) {
  .third-section--content {
    width: 90%; }
  .third-section--content--list > div {
    padding: 0 15px; } }

@media only screen and (max-width: 640px) {
  .third-section--content--list > div {
    min-width: 260px;
    width: 74.80193236714976%;
    margin: 0 auto; }
  .third-section {
    padding: 0; }
  .third-section--content {
    width: 100%; }
  .third-section--content--list {
    float: none;
    width: 100%;
    margin: 10px auto !important;
    max-width: none;
    -webkit-border-radius: 0;
    -moz-border-radius: 0;
    border-radius: 0; }
  .third-section--content--list > div img {
    margin-bottom: 70px; }
  .third-section--content--list > div h1 {
    margin-top: 0;
    font-size: 5.797vw; }
  .third-section--content--list > div p {
    font-size: 3.86vw; } }
