@charset "UTF-8";
.video-section {
  position: relative;
  /*비디오*/ }
  .video-section .video-section--text {
    position: absolute;
    top: 13.0208vw;
    left: 18.75vw;
    z-index: 1000;
    text-align: center; }
    .video-section .video-section--text p {
      font-size: 3.02083vw;
      line-height: 3.45vw;
      font-weight: bold;
      color: #fff;
      letter-spacing: -0.5px; }
    .video-section .video-section--text form {
      margin-top: 45px; }
      .video-section .video-section--text form input {
        border: none;
        background-color: #fff;
        border-top-left-radius: 32px;
        border-bottom-left-radius: 32px;
        padding: 0.67708vw 0;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        vertical-align: baseline;
        font-size: 0.7291vw;
        color: #798079;
        text-indent: 10px;
        width: 12.5vw;
        min-width: 100px; }
      .video-section .video-section--text form button {
        display: inline-block;
        vertical-align: bottom;
        border: none;
        font-size: 0.7291vw;
        background-color: #54b3a2;
        color: #fff;
        padding: 0.67708vw 0.52083vw;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        border-top-right-radius: 32px;
        border-bottom-right-radius: 32px; }
  .video-section .embed-container {
    position: relative;
    overflow: hidden;
    max-width: 100%;
    padding-bottom: 56.25%; }
    .video-section .embed-container .scroll-cover {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 999; }
  .video-section .embed-container iframe,
  .video-section .embed-container object,
  .video-section .embed-container embed {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }

@media only screen and (max-width: 640px) {
  .video-section {
    position: relative;
    /*비디오*/ }
    .video-section .video-section--text {
      position: absolute;
      top: 11.0208vw;
      left: 8.75vw;
      z-index: 999;
      text-align: center; }
      .video-section .video-section--text p {
        font-size: 16px;
        line-height: 18px;
        font-weight: bold;
        color: #fff;
        letter-spacing: -0.5px; }
      .video-section .video-section--text form {
        margin-top: 35px; }
        .video-section .video-section--text form input {
          width: 70px;
          font-size: 12px;
          padding: 6px 0; }
        .video-section .video-section--text form button {
          font-size: 10px;
          padding: 7.5px 4px; } }
