.header-section {
  position: relative;
  width: 60%;
  margin: 0 auto;
  -webkit-transition: all 0.4s ease-out;
  -moz-transition: all 0.4s ease-out;
  -ms-transition: all 0.4s ease-out;
  -o-transition: all 0.4s ease-out;
  transition: all 0.4s ease-out; }
  .header-section .__link-logo {
    display: inline-block;
    padding: 10px 0; }
    .header-section .__link-logo img {
      width: 244px;
      height: 68px; }
  .header-section--container {
    position: absolute;
    top: 0;
    right: 0;
    padding: 24px 0;
    display: inline-block; }
  .header-section .__sns-link {
    display: inline-block;
    width: 42px;
    height: 42px;
    overflow: hidden;
    text-indent: -9999px;
    margin-left: 14px; }
  .header-section .email {
    background: url("./icon_mail.png") no-repeat center/contain; }
  .header-section .facebook {
    background: url("./icon_facebook.png") no-repeat center/contain; }
  .header-section .twitter {
    background: url("./icon_twitter.png") no-repeat center/contain; }
  .header-section .__select-country {
    vertical-align: top;
    display: inline-block;
    margin-left: 14px;
    text-align: center; }
    .header-section .__select-country div {
      width: 42px;
      height: 42px;
      background: url("./icon_usa.png") no-repeat center/contain; }
    .header-section .__select-country .__down-pointer {
      display: inline-block;
      width: 16px;
      margin-top: 10px;
      height: 8px;
      background: url("./icon_point.png") no-repeat center/contain; }
    .header-section .__select-country .country-box {
      display: none;
      background: #fff; }
      .header-section .__select-country .country-box--link {
        padding: 10px 0; }
        .header-section .__select-country .country-box--link span {
          display: block;
          width: 42px;
          height: 42px;
          overflow: hidden;
          text-indent: -9999px; }
        .header-section .__select-country .country-box--link .__lang-us {
          background: url("./icon_usa.png") no-repeat center/contain; }
        .header-section .__select-country .country-box--link .__lang-korea {
          background: url("./icon_kor.png") no-repeat center/contain; }
        .header-section .__select-country .country-box--link .__lang-japan {
          background: url("./icon_jpn.png") no-repeat center/contain; }
        .header-section .__select-country .country-box--link .__lang-china {
          background: url("./icon_chi.png") no-repeat center/contain; }
  .header-section .__select-country.active .country-box {
    position: absolute;
    top: 7px;
    right: -11px;
    display: block;
    -webkit-border-radius: 32px;
    -moz-border-radius: 32px;
    border-radius: 32px;
    border: 1px solid #e5eae8;
    padding: 5px 10px;
    z-index: 99; }

@media only screen and (max-width: 1024px) {
  .header-section {
    width: 90%; } }

@media only screen and (max-width: 768px) {
  .header-section .__link-logo img {
    width: 70%;
    height: auto; }
  .header-section--container {
    padding: 19px 0; }
  .header-section .__sns-link {
    width: 30px;
    height: 30px; }
  .header-section .__select-country div {
    width: 30px;
    height: 30px; }
  .header-section .__select-country .country-box--link span {
    width: 30px;
    height: 30px; }
  .header-section .__select-country.active .country-box {
    top: 4px;
    right: -7px;
    padding: 4px 6px; } }

@media only screen and (max-width: 640px) {
  .header-section .__link-logo img {
    width: 120px; }
  .header-section--container {
    display: inline-block;
    position: absolute;
    top: 10px;
    padding: 0; }
  .header-section .__sns-link {
    margin-left: 2.8985vw; }
  .header-section .__select-country {
    position: relative; }
    .header-section .__select-country .__down-pointer {
      position: absolute;
      bottom: -15px;
      right: 6px; }
  .header-section .__select-country.active .country-box {
    top: -4px;
    padding: 0 6px; } }

@media only screen and (max-width: 320px) {
  .header-section .__link-logo img {
    width: 100px; }
  .header-section--container {
    top: 13px; }
  .header-section .__sns-link {
    width: 22px;
    height: 22px; }
  .header-section .__select-country div {
    width: 22px;
    height: 22px; }
  .header-section .__select-country .__down-pointer {
    bottom: -14px;
    right: 2px; }
  .header-section .__select-country .country-box--link a {
    width: 22px;
    height: 22px; } }
