.warning-ie-9 {
  position: relative;
  width: 100%;
  height: 100vh;
  background: url("./web-error-2-bg@2x.png") no-repeat right/cover; }
  .warning-ie-9--box {
    width: 70%;
    margin: 0 auto; }
    .warning-ie-9--box p {
      font-size: 24px;
      color: #3726af;
      text-align: left;
      line-height: 30px; }
    .warning-ie-9--box p:nth-of-type(2) {
      margin-top: 39px; }
  .warning-ie-9 .__link {
    position: relative;
    display: block;
    margin-top: 39px;
    cursor: pointer; }
    .warning-ie-9 .__link span {
      display: inline-block;
      padding: 20px 20px 20px 70px;
      -webkit-border-radius: 30px;
      border: 1px solid #3726af;
      -moz-border-radius: 30px;
      border-radius: 30px;
      background: #fff url("./error-2-icn-chrome-icon@2x.png") no-repeat 20px center/36px 36px;
      font-size: 18px;
      color: #3726af;
      font-weight: bold;
      -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
      box-sizing: border-box; }
  .warning-ie-9 .outer {
    display: table;
    width: 100%;
    height: 100vh; }
    .warning-ie-9 .outer .inner {
      display: table-cell;
      text-align: left;
      vertical-align: middle; }
      .warning-ie-9 .outer .inner .centered {
        display: inline-block;
        width: 70%; }

@media only screen and (max-width: 640px) {
  .warning-ie-9 {
    background: url("./mobile-e-10-logo@2x.png") no-repeat right bottom/90% auto;
    -webkit-transition: all 0.25s ease-out;
    -moz-transition: all 0.25s ease-out;
    -ms-transition: all 0.25s ease-out;
    -o-transition: all 0.25s ease-out;
    transition: all 0.25s ease-out; }
    .warning-ie-9--box {
      width: 90%; }
      .warning-ie-9--box p {
        margin-top: 26.57%;
        font-size: 3.3816vw;
        text-align: center;
        line-height: 4.831vw; }
      .warning-ie-9--box p:nth-of-type(2) {
        margin-top: 20px; }
    .warning-ie-9 .__link {
      margin-top: 56px; }
    .warning-ie-9 .outer .inner {
      text-align: center;
      vertical-align: top; }
      .warning-ie-9 .outer .inner .centered {
        display: inline-block;
        width: 100%; } }
